.App.dark-mode {
  background-color: #0a303e;
  color: #fff;
}

.App.light-mode {
  background-color: #ffffff;
  color: #333;
}

.container {
  margin-top: 20px;
  padding: 10px;
}

h1 {
  margin-bottom: 15px;
}

.container textarea {
  resize: none;
  border: none;
  outline: 2px solid rgba(1, 126, 168, 0.77);
  width: 100%;
  border-radius: 5px;
  margin-top: 2px;
  margin-left: 2px;
  padding: 5px;
}

.container textarea:focus {
  border: none;
  outline: 3px solid rgba(122, 142, 142, 0.8);
}

.btn {
  margin-top: 5px;
  margin-right: 10px;
}

.count {
  background-color: #F5F5F5;
  display: flex;
  justify-content: left;
  align-items: center;
  border-radius: 5px;
  padding-left: 10px;
  margin-top: 30px;
  border: 2px solid #CCCCCC;
  outline: 2px solid #000;
  width: 100%;
  color: #000;
}

.count p {
  margin-right: 25px;
  padding-top: 10px;
}

h3 {
  margin-top: 30px;
}

.myinfo img {
  width: 18rem;
  border-radius: 10px;
}

.navbar-collapse .active:hover {
  color: #CCCCCC;
}

.n3 {
  margin-top: 0;
  background-color: #F5F5F5;
}

.myinfo {
  box-sizing: border-box;
  border: 2px solid #9fe211;
  border-radius: 10px;
  margin-bottom: 10px;
  background: #e9e4e4;
  padding: 20px;
  color: #000;
}

.media {
  display: block;
}

.media-body {
  margin-top: 15px;
}

.myinfo h3 {
  margin-bottom: 10px;
  border: 2px solid #794848;
  padding: 5px 10px;
  display: inline-block;
  border-radius: 5px;
  background-color: #fff;

}

.projects {
  margin-top: 10px;
}

.skills p {
  font-weight: 450;
}

.card-footer {
  background-color: #CCCCCC;
  color: #000;
  padding: 15px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: auto;
}

.btn-space {
  background-color: #364862;
  color: #FFF;
}

.btn-space:hover {
  color: #FFF;
  background-color: #232f41;
}